<template>
    <div style="line-height: 54px;float: right">
      <span style="padding: 0 10px;float: left">{{userInfo.name}}</span>
      <span @click="loginOut" style="line-height: 54px">
        <i class="el-icon-switch-button"></i>
      </span>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                userInfo: {
                    name: ''
                }
            }
        },
        methods: {
            loginOut() {
                this.$post('/logout').then(res => {
                    this.$router.push({
                        path: '/'
                    });
                    localStorage.clear();
                })
            },
            getUserInfo() {
                this.$post('/getUserInfo').then(res => {
                    this.userInfo = res
                })
            }
        },
        mounted() {
            this.getUserInfo()
        }
    }
</script>

<style scoped>

</style>



